import React from "react";
import { 
    FormControl, 
    Select, 
    MenuItem
} from '@material-ui/core'

const LessonFormSubject = ({ formValues, handleChange }) => {
    return (
        <FormControl>   
            <Select
                MenuProps={{
                    anchorOrigin: {
                    vertical: "bottom",
                    horizontal: "left"
                    },
                    transformOrigin: {
                    vertical: "top",
                    horizontal: "left"
                    },
                    getContentAnchorEl: null
                }}
                value={formValues.contents.subject}
                onChange={handleChange}
                name='subject'
            >
                <MenuItem key='finnish' value='finnish'>
                    Finnish
                </MenuItem>
                <MenuItem key='maths' value='maths'>
                    maths
                </MenuItem>
                <MenuItem key='science' value='science'>
                    science
                </MenuItem>
                <MenuItem key='religion' value='religion'>
                    religion
                </MenuItem>
            </Select>
        </FormControl>
    )
}

export default LessonFormSubject