import React from "react";
import { FormControl, Select, Box, Chip, MenuItem } from '@material-ui/core'

const LessonFormContent = ({ handleChange, formValues, filteredContents }) => {
    return (
        <FormControl sx={{ m: 1, width: 300 }}>
            <Select
                MenuProps={{
                    anchorOrigin: {
                    vertical: "bottom",
                    horizontal: "left"
                    },
                    transformOrigin: {
                    vertical: "top",
                    horizontal: "left"
                    },
                    getContentAnchorEl: null
                }}
                labelId="demo-multiple-chip-label"
                id="demo-multiple-chip"
                multiple
                name='content'
                value={formValues.contents.content ? formValues.contents.content : []}
                onChange={handleChange}
                renderValue={(selected) => (
                    <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                    {selected.map((value) => (
                        <Chip key={value} label={value} />
                    ))}
                    </Box>
                )}
                >
                {filteredContents.map((name) => (
                    <MenuItem
                        key={name.content}
                        value={name.content}
                    >
                        {name.content}
                    </MenuItem>
                ))}
            </Select>
        </FormControl>
    )
}

export default LessonFormContent