import lessonService from '../services/lessons'

export const initializeLessons = () => {
    return async dispatch => {
        const lessons = await lessonService.getAll()
        dispatch({
            type: 'INIT_LESSONS',
            data: lessons
        })
    }
}

const lessonReducer = (state = [], action) => {
    switch(action.type) {
    case 'INIT_LESSONS':
        return action.data
    default:
        return state
    }
}

export default lessonReducer