import { Avatar, Box, Button, TextField } from "@material-ui/core";
import React, { useState } from "react";
import AssignmentIcon from "@material-ui/icons/Assignment"
import LessonList from "./LessonList";

const Login = () => {
    const [lessons, setLessons] = useState(false)

    if(lessons) {
        return <LessonList />
    }

  return (
    <div>
        <Box
            display='flex'
            alignItems='center'
            justifyContent='center'
            height='100vh'
            flexDirection='column'>
            <Box
                flexDirection='column'
                display='flex'
                alignItems='center'>
                <Box
                    fontSize='500%'
                    letterSpacing='10px'
                    fontStyle='italic'
                    display='flex'
                    flexDirection='row'
                    className='lessons'>
                    <Avatar
                        variant='rounded'
                        style= {{ 
                            backgroundColor: '#8bc34a',
                            height:'20%',
                            width: '6%'
                        }}>
                        <AssignmentIcon />
                    </Avatar>
                    Lessons
                </Box>
                <Box 
                    padding={2}
                    height='25%'
                    width='85%'>
                    <Box>
                        <TextField 
                            label='username'
                            autoFocus
                            required 
                            fullWidth
                            className='username'
                        />
                    </Box>
                    <Box>
                        <TextField 
                            label='password' 
                            type='password'
                            required
                            fullWidth
                            className='password'
                        />
                    </Box>
                    <Button 
                        className='submit-btn'
                        variant='contained'
                        onClick={()=>setLessons(true)}
                    >
                        Log in
                    </Button>
                    <Box display='flex' justifyContent='center' paddingTop='20%'>
                        <Button 
                            variant='contained'
                            style= {{ backgroundColor: '#8bc34a' }}
                        >
                            create new user
                        </Button>
                    </Box>
                </Box>
            </Box>
        </Box>
    </div>
    
    
  );
}

export default Login;
