import React from "react";
import { TextField } from '@material-ui/core'

const LessonFormTopic = ({ handleTextInputChange }) => {
    return (
            <TextField 
                id='topic-input'
                label=' '
                autoFocus
                required 
                fullWidth
                className='topic'
                name='topic'
                onChange={handleTextInputChange}                            
            />
    )
}

export default LessonFormTopic