import React from "react";
import { Link } from "react-router-dom";
import { 
    Box, Typography, TableContainer, Table, TableRow, TableCell, TableBody,
    TableHead
} from "@material-ui/core";
import { useSelector } from "react-redux";
import LessonForm from "./LessonForm";

const LessonList = () => {
    const lessons = useSelector(state => state.lessons)

    return (
        <>  
        <LessonForm />
        <Box display='flex' flexDirection='column' padding='20px'>
            {// Displays logged in user and log out button
            <Box display='flex' justifyContent='end' padding='20px'>
                <Typography><i>User logged in</i></Typography>
            </Box>
            }
            {// Displays lessons and create new lesson links
            <Box display='flex' justifyContent='space-around' padding='20px'>
                <Link to='/lessons' style={{textDecoration: 'none', color: '#8bc34a'}}>
                    <Typography  variant='h5' ><i>Lessons</i></Typography>
                </Link>
                <Link to='/newlesson' style={{textDecoration: 'none', color: '#8bc34a'}}>
                    <Typography  variant='h5'>Create new lesson</Typography>
                </Link>
            </Box>
            }
            {// Displays created lessons
            <Box display='flex' justifyContent='flex-start' padding='20px'>
                <TableContainer>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>Topic</TableCell>
                                <TableCell>Subject</TableCell>
                                <TableCell>Content</TableCell>
                                <TableCell>Name</TableCell>
                                <TableCell>Class</TableCell>
                                <TableCell>Date</TableCell>
                                <TableCell>Duration</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {lessons.map(lesson =>
                                <TableRow key={lesson.id}>
                                    <TableCell>
                                            <Typography>
                                                {lesson.topic}
                                            </Typography>
                                    </TableCell>
                                    <TableCell>
                                            <Typography>
                                                {lesson.contents.map(c => c.subject)}
                                            </Typography>
                                    </TableCell>
                                    <TableCell>
                                            <Typography>
                                                {lesson.contents.map(c => c.content)}
                                            </Typography>
                                    </TableCell>
                                    <TableCell>
                                            <Typography>
                                                {lesson.students.map(s => s.name)}
                                            </Typography>
                                    </TableCell>
                                    <TableCell>
                                            <Typography>
                                                {lesson.students.map(s => s.class)}
                                            </Typography>
                                    </TableCell>
                                    <TableCell>
                                            <Typography>
                                                {lesson.date}
                                            </Typography>
                                    </TableCell>
                                    <TableCell>
                                            <Typography>
                                                {lesson.duration}
                                            </Typography>
                                    </TableCell>
                                </TableRow>
                            )}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Box>
            }
        </Box>
        
        </>
    )
}

export default LessonList